<template>
    <ion-page>
        <div class="container mt-4">
            <ion-toolbar>
                <ion-buttons slot="start">
                    <ion-back-button default-href="/"></ion-back-button>
                </ion-buttons>
                <ion-button class="sign" slot="end" router-link="/signup"> Sign Up
                </ion-button>
            </ion-toolbar>
        </div>
            
        <ion-content>
            <body>
            <ion-grid>
                <div class="container">
                    <h1 class="pl-3 login_user">Login User</h1>
                    <h1 class="pl-3 welcome_back">Welcome back</h1>
                    <p class="pl-3 continue">Sign in to continue</p>
                    <ion-row class="text-center">
                        <ion-col size="12" size-md="12">
                            <div class="form">
                                <label for="mobile"><ion-icon :icon="call" slot="start"></ion-icon></label>
                                <input type="number" id="mobile" placeholder="Enter Mobile Number">
                            </div>
                            <div class="form">
                                <label for="password"><ion-icon :icon="lockClosed" slot="start"></ion-icon></label>
                                <input type="password" id="password" placeholder="**********">
                            </div>
                            <ion-button class="login_btn">
                                Log in
                            </ion-button>
                        </ion-col>
                        <ion-col>
                            <p class="forgot" @click="openModal"><span class="forgot_span">Forgot password?</span></p>
                        </ion-col>
                    </ion-row>
                </div>
            </ion-grid>
            </body>
        </ion-content>
    </ion-page>
</template>

<script>
import {
    IonPage,
    IonContent,
    IonToolbar,
    IonCol,
    IonGrid,
    IonRow,
    IonButton,
    IonButtons,
    IonBackButton,
    IonIcon,
    modalController
    } from '@ionic/vue';
import {call,lockClosed} from 'ionicons/icons';
import Modal from '../views/Modal.vue';
export default {
    components: {
        IonPage,
        IonContent,
        IonToolbar,
        IonCol,
        IonGrid,
        IonRow,
        IonButton,
        IonButtons,
        IonBackButton,
        IonIcon
    },
    data(){
        return{call, lockClosed}
    },
    methods: {
        async openModal() {
            const modal = await modalController
                .create({
                    component: Modal,
                    cssClass: 'forgot_password_modal',
                })
            return modal.present();
        },
    }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600&display=swap');
    .sign{
        text-transform: unset;
    }
    .login_user{
        font-family: 'Roboto Condensed', sans-serif;
        font-weight: 700;
        letter-spacing: 1.3px;
        font-size: 32px;
    }
    .welcome_back{
        font-weight: 400;
    }
    .continue{
        font-weight: 200;
        font-family: 'Montserrat', sans-serif;
    }
    label{
        background-color: var(--ion-color-primary);
        color:white;
        border-radius: 100%;
        padding: 7px 7px 2px 7px;
    }
    input{
        margin: 20px;
        border:none;
        outline: none;
        text-align: center;
    }
    ::placeholder{
        font-weight: 700;
    }
    .login_btn{
        --padding-start:100px;
        --padding-end:100px;
        text-transform: unset;
    }
    .forgot{
        margin-left: 10%;
        font-size: 15px;
        font-family: 'Montserrat', sans-serif;
    }
    .forgot_span:hover{
        text-decoration: underline;
        cursor: pointer;
    }
    body{
        background-image: url('../../public/assets/images/buildings.png');
        background-position:bottom;
        background-repeat: no-repeat;
    }


    /* Media Queries */

    /* Galaxy Fold, motog4, galaxy s5, iphone5/se, iphone 6/7/8, iphoneX  */
    @media(min-width:280px) and (max-width:400px){
        .login_user{
            font-size: 30px;
        }
        .welcome_back{
            font-size: 20px;
        }
        .continue{
            font-size: 12px;
        }
        label{
            font-size: 12px;
        }
        input{
            margin: 10px;
        }
        .login_btn{
            --padding-start:90px;
            --padding-end:90px;
        }
        .forgot{
            font-size: 12px;
            margin-left: 40%;
        }
    }

    /* pixel2, pixel2xl, iphone6,7,8 plus, surface duo */
    @media(min-width:401px) and (max-width:600px){
        .forgot{
            font-size: 12px;
            margin-left: 35%;
        }
    }

    /* iPad */
    @media(min-width:768px) and (max-width:1023px){
        .forgot{
            font-size: 12px;
            margin-left: 20%;;
        }
    }
</style>