import { createRouter, createWebHistory } from '@ionic/vue-router';
import Home from '../views/Home.vue'
import Menu from '../views/Menu.vue'
import Login from '../views/Login.vue'
import Login2 from '../views/Login2.vue'
import Register from '../views/Register.vue'
import LoginUser from '../pages/LoginUser.vue'
import SignUp from '../pages/SignUp.vue'
import Storage from '../pages/Storage.vue'
import TabsHome from '../views/TabsHome.vue'
import Schedule from '../components/Schedule.vue'
import Speakers from '../components/Speakers.vue'

import HomeStudents from '../students/pages/HomeStudents.vue';
import Page1 from '../students/pages/Page1.vue';
import Page2 from '../students/pages/Page2.vue';
import Page3 from '../students/pages/Page3.vue';

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    component: Home
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/login2',
    component: Login2
  },
  {
    path: '/loginuser',
    component: LoginUser
  },
  {
    path: '/signup',
    component: SignUp
  },
  {
    path: '/register',
    component: Register
  },
  {
    path: '/storage',
    component: Storage
  },
  {
    path: '/tabshome',
    component: TabsHome
  },
  {
    path: '/tabs/schedule',
    component: Schedule
  },
  {
    path: '/tabs/speakers',
    component: Speakers
  },
  {
    path: '/students/homestudents',
    component: HomeStudents
  },
  {
    path: '/students/page1',
    component: Page1
  },
  {
    path: '/students/page2',
    component: Page2
  },
  {
    path: '/students/page3',
    component: Page3
  },
  {
    path: '/menu',
    component: Menu
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
