<template>
    <ion-page>
        <ion-menu side="start" type="push" content-id="main-content">
            <ion-header>
                <ion-toolbar translucent>
                <ion-title>Menu</ion-title>
                </ion-toolbar>
            </ion-header>
            <ion-content>
                <ion-list>
                    <ion-item class="cust">
                        <ion-icon :icon="mail" slot="start"></ion-icon>
                        <ion-label>Inbox</ion-label>
                    </ion-item>
                    <ion-item>
                        <ion-icon :icon="paperPlane" slot="start"></ion-icon>
                        <ion-label>Outbox</ion-label>
                    </ion-item>
                    <ion-item>
                        <ion-icon :icon="heart" slot="start"></ion-icon>
                        <ion-label>Favorites</ion-label>
                    </ion-item>
                    <ion-item>
                        <ion-icon :icon="archive" slot="start"></ion-icon>
                        <ion-label>Archived</ion-label>
                    </ion-item>
                    <ion-item>
                        <ion-icon :icon="trash" slot="start"></ion-icon>
                        <ion-label>Trash</ion-label>
                    </ion-item>
                    <ion-item>
                        <ion-icon :icon="warning" slot="start"></ion-icon>
                        <ion-label>Spam</ion-label>
                    </ion-item>
                    <ion-item @click="$router.replace('/home')">
                        <ion-icon :icon="home" slot="start"></ion-icon>
                        <ion-label>Back Home</ion-label>
                    </ion-item>
                </ion-list>
            </ion-content>
        </ion-menu>

        <div class="ion-page" id="main-content">
            <ion-header>
                <ion-toolbar color="primary">
                    <ion-buttons slot="start">
                        <ion-menu-button></ion-menu-button>
                    </ion-buttons>
                    <ion-title>Inbox</ion-title>
                </ion-toolbar>
            </ion-header>
            <ion-content>
                <ion-button @click="openModal" color="dark" expand="block">Open Modal</ion-button>
            </ion-content>
        </div>
    </ion-page>
</template>

<script>
import { 
  IonPage,IonContent, 
  IonHeader, 
  IonItem, 
  IonList, 
  IonMenu,
  IonTitle, 
  IonToolbar,
  IonButtons,
  IonButton,
  IonIcon,
  IonLabel,
  IonMenuButton,
  menuController,
  modalController
} from '@ionic/vue';
import Modal from './Modal.vue'

import {mail, paperPlane, heart, archive, trash, warning, home} from 'ionicons/icons';
export default {
    name:"Menu",
    components: {
        IonPage,IonContent, 
        IonHeader, 
        IonItem, 
        IonList, 
        IonMenu,
        IonTitle, 
        IonToolbar,
        IonButtons,
        IonButton,
        IonIcon,
        IonLabel,
        IonMenuButton},
    data(){
        return{
            mail, paperPlane, heart, archive, trash, warning, home
        }
    },
    methods: {
        openFirst() {
            menuController.enable(true, 'first');
            menuController.open('first');
        },
        openEnd() {
            menuController.open('end');
        },
        openCustom() {
            menuController.enable(true, 'custom');
            menuController.open('custom');
        },
        async openModal() {
            const modal = await modalController
                .create({
                    component: Modal,
                    cssClass: 'my-custom-class',
                    componentProps: {
                        title: 'New Title'
                    },
                })
            return modal.present();
        }
    }
};
</script>